@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700;900&display=swap');
@import '../../variables';

html,body{
    font-family:'Roboto', sans-serif;
}

@media only screen and (max-width:1024px){
    .service-Container{
        background-color:white;
        padding:20px 25px;
        width:80%;
        margin-left:10%;
        border-radius:5px;
        box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        margin-top:30px;
    
        h2{
            padding-bottom:10px;
            color:#03a9f4;
        }
    
        h5{
            color:black;
        }
    
        button{
                width:50%;
                padding-top:0.5rem;
                padding-bottom:0.5rem;
                padding-left:0;
                padding-right:0;
                background-color:#03a9f4;
                color:white;
                border-style:none;
                margin-top:1.8vh;
                box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
        }
        .icon-service{
            font-size:2rem;
            color:black;
        }
    }

}

@media only screen and (min-width:1024px){
    .service-Container{
        background-color:white;
        padding:20px 25px;
        width:33%;
        margin-left:0.33%;
        border-radius:5px;
        box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        margin-top:30px;
        float:left;

        h2{
            padding-bottom:10px;
            color:#03a9f4;
        }
    
        h5{
            color:black;
        }
    
        button{
                width:50%;
                padding-top:0.5rem;
                padding-bottom:0.5rem;
                padding-left:0;
                padding-right:0;
                background-color:#03a9f4;
                color:white;
                border-style:none;
                margin-top:1.8vh;
                box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
        }

        .icon-service{
            font-size:2rem;
            color:black;
        }
    }

}